import React, {useState} from 'react';
import TreeView from '@material-ui/lab/TreeView';
import TreeItem from '@material-ui/lab/TreeItem';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import Modal from "react-bootstrap/Modal";
import Button from 'react-bootstrap/Button';
import "bootstrap/dist/css/bootstrap.min.css";

function FileSystem(props) {

    const [newFileModalTrigger, setNewFileModalTrigger] = useState(false);
    const [newFileName, setNewFileName] = useState("")
    const [newFolderModalTrigger, setNewFolderModalTrigger] = useState(false);
    const [newFolderName, setNewFolderName] = useState("")

    const handleOnClick = (boolTrigger) => {
        boolTrigger ? setNewFileModalTrigger(true) : setNewFolderModalTrigger(true)
    }

    const handleOnClickCreate = (boolTrigger) => {
        if (boolTrigger) {
            setNewFileModalTrigger(false)
            addNewFile(newFileName)
        } else {
            setNewFolderModalTrigger(false)
            addNewFolder(newFolderName)
        }
    }

    const handleOnClickCancel = (boolTrigger) => {
        boolTrigger ? setNewFileModalTrigger(false) : setNewFolderModalTrigger(false)
    }

    const addNewFile = (fileName) => {
        props.fileSystem["fileChildren"].push({
            fileName: fileName,
            filePath: props.fileSystem["filePath"] + "/" + fileName
        })
    }

    const addNewFolder = (folderName) => {
        props.fileSystem["fileChildren"].push({
            fileName: folderName,
            filePath: props.fileSystem["filePath"] + "/" + folderName,
            fileChildren: [{}]
        })
    }

    const fileSystemView = (currNode) => (
        <TreeItem nodeId={currNode["filePath"]} label={currNode["fileName"]}>
            {currNode["fileChildren"] !== undefined ?
                currNode["fileChildren"].map((childNode) => fileSystemView(childNode)) :
                null}
        </TreeItem>
    )

    return (
        <div>
            {newFileModalTrigger ?
                <Modal show={newFileModalTrigger} onHide={() => {
                    handleOnClickCancel(true)
                }}>
                    <Modal.Header>
                        <Modal.Title>Create New File</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <form>
                            <label>New File Name:</label><br/>
                            <input type="text"
                                   onChange={(event) => {
                                       setNewFileName(event.target.value)
                                   }}/>
                        </form>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={() => {
                            handleOnClickCancel(true)
                        }}>
                            Close
                        </Button>
                        <Button variant="primary" onClick={() => {
                            handleOnClickCreate(true)
                        }}>
                            Create
                        </Button>
                    </Modal.Footer>
                </Modal> :
                null}
            {newFolderModalTrigger ?
                <Modal show={newFolderModalTrigger} onHide={() => {
                    handleOnClickCancel(false)
                }}>
                    <Modal.Header>
                        <Modal.Title>Create New Folder</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <form>
                            <label>New Folder Name:</label><br/>
                            <input type="text"
                                   onChange={(event) => {
                                       setNewFolderName(event.target.value)
                                   }}/>
                        </form>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={() => {
                            handleOnClickCancel(false)
                        }}>
                            Close
                        </Button>
                        <Button variant="primary" onClick={() => {
                            handleOnClickCreate(false)
                        }}>
                            Create
                        </Button>
                    </Modal.Footer>
                </Modal> :
                null}
            <div>
                <Button style={{margin: "1px"}} variant="secondary" onClick={() => {
                    handleOnClick(true)
                }}>
                    New File
                </Button>
                <Button style={{margin: "1px"}} variant="secondary" onClick={() => {
                    handleOnClick(false)
                }}>
                    New Folder
                </Button>
                <TreeView
                    defaultCollapseIcon={<ExpandMoreIcon/>}
                    defaultExpandIcon={<ChevronRightIcon/>}
                    defaultExpanded={[""]}
                >
                    {fileSystemView(props.fileSystem)}
                </TreeView>
            </div>
        </div>
    )
}

export default FileSystem;
