import React, {useState} from "react";
import {useAuth0} from "@auth0/auth0-react";
import './Profile.css';

const Profile = () => {
    const { user, isAuthenticated } = useAuth0();


    return (
        isAuthenticated && (
        <div>
            <img className={"userInfo"} src={user.picture} alt={"./graph.png"}/>
            <div className={"userInfo"}>{user.given_name}</div>
        </div>
        )
    )

}
export default Profile;